.header {
  background-color: #282c34;
  padding: 8px 0px;
}

.links {
  display: flex;
  gap: 48px;
  margin: 0px 24px;
}
.link {
  cursor: pointer;
  font-size: 24px;
  text-decoration: none;
  color: #fff;
  position: relative;
}
.activeLink {
  color: #61dafb;
  > span {
    color: #fff;
  }
}
